import React, { useEffect } from "react";
import { ProjectOne, ProjectTwo, ProjectThree, ProjectFour } from "./Data";
import { GroupOne, GroupTwo, GroupThree, GroupFour } from "./Data";
import MyGallery from "./Gallery";
import "./ProjectDetail.css";
import Footer from "./Footer";
import DividerProjects from "./DividerProjects";
import AOS from "aos";
import "aos/dist/aos.css";

const ProjectDetail = ({ match }) => {
  const projects = [ProjectOne, ProjectTwo, ProjectThree, ProjectFour];
  let clickedProject = {};
  projects.forEach((project) => {
    if (project.id === match.params.id) {
      clickedProject = project;
    }
  });
  const groups = [GroupOne, GroupTwo, GroupThree, GroupFour];
  let clickedGroup = {};
  let clickedGroupNames = [];
  let clickedGroupImages = [];
  let clickedGroupLinks = [];
  let clickedGroupEmails = [];
  groups.forEach((group) => {
    if (group.idProject === match.params.id) {
      clickedGroup = group;
    }
  });
  for (const property in clickedGroup) {
    clickedGroupImages.push(`${clickedGroup[property]}`);
    clickedGroupLinks.push(`${clickedGroup[property]}`);
    clickedGroupEmails.push(`${clickedGroup[property]}`);
    clickedGroupNames.push(`${property}`);
  }
  clickedGroupEmails.splice(0, 14);
  clickedGroupLinks.splice(0, 8);
  clickedGroupLinks.splice(6);
  clickedGroupImages.splice(8);
  clickedGroupImages.splice(0, 2);
  clickedGroupNames.splice(0, 2);
  clickedGroupNames.splice(6);
  console.log("emails", clickedGroupEmails);
  console.log("images", clickedGroupImages);
  console.log("links", clickedGroupLinks);
  console.log("names", clickedGroupNames);
  useEffect(() => {
    AOS.init({});
  }, []);
  return (
    <div>
      <div className="backgroundimage">
        <h1>{clickedProject.title}</h1>
      </div>
      <div className="headline-wrapper">
        <div>
          <h1>{clickedProject.subtitle}</h1>
        </div>
        <div>
          <h1>{clickedProject.headline}</h1>
        </div>
      </div>
      <DividerProjects {...clickedProject} />

      <h1 className="margin-bigger">Tim Koordinatora</h1>
      <div className="person-cnt">
        <div>
          <h1>{clickedGroupNames[0]}</h1>
          <img
            className="person-img"
            src={`../${clickedGroupImages[0]}`}
            alt=""
          />
          <p>{clickedGroupEmails[0]}</p>{" "}
          <a href={clickedGroupLinks[0]}>
            <i className="fab fa-linkedin person-icon" />
          </a>
        </div>
        <div>
          <h1>{clickedGroupNames[1]}</h1>
          <img
            className="person-img"
            src={`../${clickedGroupImages[1]}`}
            alt=""
          />
          <p>{clickedGroupEmails[1]}</p>{" "}
          <a href={clickedGroupLinks[1]}>
            <i className="fab fa-linkedin person-icon" />
          </a>
        </div>
        <div>
          <h1>{clickedGroupNames[2]}</h1>
          <img
            className="person-img"
            src={`../${clickedGroupImages[2]}`}
            alt=""
          />  
          <p>{clickedGroupEmails[2]}</p>{" "}
          <a href={clickedGroupLinks[2]}>
            <i className="fab fa-linkedin person-icon" />
          </a>
        </div>
        <div>
          <h1>{clickedGroupNames[3]}</h1>
          <img
            className="person-img"
            src={`../${clickedGroupImages[3]}`}
            alt=""
          />
          <p>{clickedGroupEmails[3]}</p>{" "}
          <a href={clickedGroupLinks[3]}>
            <i className="fab fa-linkedin person-icon" />
          </a>
        </div>
        <div>
          <h1>{clickedGroupNames[4]}</h1>
          <img
            className="person-img"
            src={`../${clickedGroupImages[4]}`}
            alt=""
          />
          <p>{clickedGroupEmails[4]}</p>{" "}
          <a href={clickedGroupLinks[4]}>
            <i className="fab fa-linkedin person-icon" />

          </a>
        </div>
        <div>
          <h1>{clickedGroupNames[5]}</h1>
          <img
            className="person-img"
            src={`../${clickedGroupImages[5]}`}
            alt=""
          />
          <p>{clickedGroupEmails[5]}</p>
          <a href={clickedGroupLinks[5]}>
            <i className="fab fa-linkedin person-icon" />
          </a>
        </div>
      </div>

      <MyGallery project={clickedProject} />
      <Footer />
    </div>
  );
};

export default ProjectDetail;
